
import React from 'react';

function LoginForm() {
    const handleSubmit = event => {
        event.preventDefault();
        alert("You are logged in.");
    }
    return(
    <div>
        <h2>Login</h2>
        <form onSubmit={handleSubmit} >
            <fieldset>
                <label>
                    <p>Name</p>
                    <input type="text" />
                </label>
                <label>
                    <p>Password</p>
                    <input type="password" />
                </label>
            </fieldset>
            <button type="submit">Submit</button>
        </form>
    </div>
    )
}

export default LoginForm;